.navbar-container {
  display: flex;
  padding: 10px 30px 10px 15px;
  justify-content: space-between;
  color: hsl(0, 0%, 12%);
  background-color: hsl(155, 40%, 84%);
}

.main-logo {
  font-weight: 800;
  font-size: 1.75rem;
}

.nav-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-container button {
  border: none;
  font-weight: 600;
  font-size: 0.8rem;
  cursor: grab;
  padding: 5px 10px;

  background-color: transparent;
}

.nav-container .save-btn {
  border: 1px solid hsl(0, 0%, 12%);
  border-radius: 4px;
  margin-left: 5px;
  border-radius: 5px;
}

.nav-container .save-btn:hover {
  background-color: hsl(155, 40%, 74%);
}

.nav-container .help-btn {
  border-bottom: 2px solid transparent;
}

.nav-container .help-btn:hover {
  border-bottom: 2px solid hsl(0, 0%, 12%);
}
