.preview-container {
  width: 210mm;
  height: 297mm;
  margin: 0px;
  border: 1px solid hsl(0, 0%, 100%);
  background-color: white;
  line-height: normal;
}

.preview-container li {
  list-style: disc;
}

.show-border {
  border: 1px solid hsl(0, 100%, 90%);
}

.resume-content {
  height: 275mm;
  width: 181mm;
  margin: 36px 55px;
  font-family: "Open Sans", sans-serif;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
}

.preview-container .header {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-align: center;
  border-bottom: 3px solid black;
}

.preview-container .header .name {
  font-weight: 600;
  font-size: 57px;
  padding-top: 39px;
  letter-spacing: 13px;
}

.preview-container .header .jobTitle {
  padding-top: 22px;
  padding-bottom: 30px;
  letter-spacing: 5px;
  font-size: 14px;
}

.preview-container .contact {
  display: flex;
  justify-content: space-between;
  padding: 6px 8px;
  font-size: 13px;
  align-items: center;
  border-bottom: 3px solid black;
}

.contact > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact > div > svg {
  width: 20px;
}

.profile {
  padding-top: 38px;
}

.title {
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 4px;
  padding-bottom: 8px;
}

section .spacer {
  width: 40px;
  height: 3px;
  background-color: black;
}

.profile .content {
  padding-top: 16px;
  font-size: 13.5px;
  line-height: 21px;
}

.container {
  display: flex;
  margin-top: 30px;
  padding-bottom: 22px;
}

.left-container {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 168px;
  padding-right: 15px;
  border-right: 1px solid black;
}

.right-container {
  padding-left: 30px;
  flex-grow: 2;
}

.info-container {
  padding-top: 30px;
}

.info-container .info-item {
  padding-top: 16px;
  font-size: 13px;
}

.info-container .info-item .name {
  font-weight: 600;
}

.info-container .info-item .details {
  padding-top: 2px;
}

.info-container .info-item .year {
  padding-top: 3px;
}

.list-container {
  padding-top: 30px;
}

.list-container .list-item-container {
  padding-left: 15px;
  margin: 0px;
  padding-top: 5px;
}

.list-container .list-item-container .list-item:first-child {
  padding-top: 16px;
}

.list-container .list-item-container .list-item {
  font-size: 13px;
  padding-left: 0px;
  margin: 0px;
  padding-top: 10px;
}

.list-container .list-item-container .list-item .name {
  padding-bottom: 2px;
}

.list-container .list-item-container .list-item .level-proficiency {
  font-style: italic;
  font-size: 12px;
}

.list-item .proficiency-bar-container {
  width: 100px;
  height: 6px;
  border: 1px solid black;
  margin-top: 2px;
}

.proficiency-bar-fill {
  background-color: black;
  height: 4px;
}

.list-item .proficiency-bar-container .width-0 {
  width: 0px;
}

.list-item .proficiency-bar-container .width-1 {
  width: 20px;
}

.list-item .proficiency-bar-container .width-2 {
  width: 40px;
}

.list-item .proficiency-bar-container .width-3 {
  width: 60px;
}

.list-item .proficiency-bar-container .width-4 {
  width: 80px;
}

.list-item .proficiency-bar-container .width-5 {
  width: 100px;
}

.experience-container {
  padding-top: 30px;
}

.experience-container .experience-item {
  padding-top: 16px;
  font-size: 13px;
}

.experience-container .experience-item .job-title {
  font-weight: 600;
}

.experience-container .experience-item .info {
  padding-top: 2px;
  /* font-style: italic; */
}

.experience-container .experience-item .summary {
  padding-top: 4px;
  line-height: 20px;
}

.experience-container .experience-item .details-container {
  margin: 0px;
  padding-top: 4px;
  padding-left: 30px;
}

.experience-container .experience-item .details-container li {
  padding-left: 4px;
}

.reference-container {
  padding-top: 33px;
}

.reference-items-container {
  padding-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.reference-items-container .reference-item {
  /* background-color: hsl(0, 0%, 90%); */
  border: 1px solid hsl(0, 0%, 50%);
  padding: 15px;
  flex-basis: 212px;
  margin-bottom: 20px;
}

.reference-items-container .reference-item .name {
  font-weight: 600;
  font-size: 13px;
  padding-bottom: 3px;
}

.reference-items-container .reference-item .job-title {
  font-weight: 600;
  font-size: 13px;
  padding-bottom: 6px;
}

.reference-items-container .reference-item .company-name {
  font-size: 11.5px;
  padding-bottom: 3px;
}

.reference-items-container .reference-item .phone {
  font-size: 11.5px;
  padding-bottom: 3px;
}

.reference-items-container .reference-item .email {
  font-size: 11.5px;
}

/* always put these at the bottom */

.right-container section:first-child {
  padding-top: 0px;
}

.left-container section:first-child {
  padding-top: 0px;
}
